import React, {useState, useEffect} from 'react';
import styled from "styled-components";
// import logo from './logo.svg';
import './App.css';
import Typewriter from './TypeWriter';
import Modal from './Modal';
import {
  Button,
} from '@mui/material';


function App() {
  const [title, setTitle] = useState("You already know what time it is");
  const [subtitle, setSubtitle] = useState("");
  const [typed, setTyped] = useState("Valentine's!!");
  const [open, setOpen] = useState(false);
  const [buttonVisibility, setButtonVisibility] = useState(0);
  const [modalTitle, setModalTitle] = useState("You made the right choice!!!");
  const [modalSubtitle, setModalSubtitle] = useState("We will have so much fun together. I can't wait to see you.");
  const [buttonText, setButtonText] = useState("WhooHoooo!!!");

  const handleOnClose = () => {
    setOpen(false);
  }
  useEffect(() => {
    const timer = setTimeout(() => {
      setTitle("Will you be my valentine?");
      // setSubtitle("I hope you say yes, even though there is only 1 answer");
      setTyped("");
      setButtonVisibility(1);
    }, 3000);
    // clear timer if the component unmount
    return;
  }, []);


  return (
    <div className="App">
      <header className="App-header">
        <Title>{title}</Title>
        <SubTitle>{subtitle}</SubTitle>
        <div style={{display: 'inline-block'}}>
          <Button onClick={() => {
            setOpen(true);
            setModalTitle("Happy 7th!!!");
            setModalSubtitle("We will have so much fun together. I can't wait to see you.");
            setButtonText("WhooHoooo!!!");
          }} style={{
            backgroundColor: "#000",
            color: "#fff",
            marginTop: "2rem",
            opacity: buttonVisibility,
            display: "inline-block"
          }}>YESSS</Button>
          <Button onClick={() => {
            setOpen(true);
            setModalTitle("Whoops!!!");
            setModalSubtitle("Wrong answer bae!!! what is this!!");
            setButtonText("Try Again! Huh!");
          }} style={{
            backgroundColor: "#000",
            color: "#fff",
            marginTop: "2rem",
            opacity: buttonVisibility,
            display: "inline-block",
            marginLeft: "2rem"
          }}>Noo</Button>
        </div>
        
        <Typewriter txt={typed} />
        <Modal
          open={open}
          onClose={handleOnClose}
          ariaLabelledBy="modal-modal-title"
          ariaDescribedBy="modal-modal-description"
          title={modalTitle}
          subtitle={modalSubtitle}
          buttonText={buttonText}
        />
      </header>
    </div>
  );
}

const Title = styled.span`
  font-size: 3rem;
  color: #fff;
  margin-bottom: 2rem;
  display: block;
`
const SubTitle = styled.span`
  font-size: 2rem;
  color: #fff;
  display: block;
`


export default App;
