import React, { useState, useEffect } from 'react';
import styled, {keyframes} from "styled-components";

const Typewriter = ({txt}: {txt: string}) => {
    const [text, setText] = useState(txt.split(""));
  return (
        <Wrapper>{
            text.map((letter, index) => {
                return (
                    <span
                        key={index}
                        style={{ animationDelay: `${index/10}s` }}
                    >
                        {letter}
                    </span>
                );
            })
        }</Wrapper>
    );
};

const Animation = keyframes`
    0% {
        opacity: 0; transform: translateY(-100px); filter: blue(10px)
    },
    // 25% {
    //     opacity: 1; transform: translateY(0px); filter: blue(0px)
    // },
    50% {
        opacity: 1; transform: translateY(0px); filter: blue(0px)
    },
    75% {
        opacity: 1; transform: translateY(0px); filter: blue(0px)
    },
    100% {
        opacity: 0; transform: filter: blue(0px)
    }
`

const Wrapper = styled.span`
    display: inline-block;
    span {
        display: inline-block;
        opacity: 0;
        animation-name: ${Animation};
        animation-duration: 3s;
        animation-fill-mode: forwards;
        font-size: 10rem;
        color: #fff;
    }
`

export default Typewriter;
