
import React from 'react';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogContentText,
    DialogActions,
    Button,
} from '@mui/material';

const Modal = ({ open, onClose, ariaLabelledBy, ariaDescribedBy, title, subtitle, buttonText }: { open: boolean, onClose: () => void, ariaLabelledBy: string, ariaDescribedBy: string, title: string, subtitle: string, buttonText: string }) => {
    return (
        <Dialog
            open={open}
            onClose={onClose}
            aria-labelledby={ariaLabelledBy}
            aria-describedby={ariaDescribedBy}
        >
            <DialogTitle id="modal-modal-title">{title}</DialogTitle>
            <DialogContent>
                <DialogContentText id="modal-modal-description">
                    {subtitle}
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>{buttonText}</Button>
                {/* <Button onClick={onClose}>Subscribe</Button> */}
            </DialogActions>
        </Dialog>
    );
};

export default Modal;